<template>
    <div id="dashboard">
          <ul class="collection">
            <li class="collection-item avatar" v-for="employee in employees" v-bind:key="employee.id">
            <!-- <img src="" alt="" class="circle">  -->
            <div class="circle light-blue center">{{employee.emp_id}}</div>
            <span class="title">{{employee.name}}</span>
            <p>{{employee.dept}} <br> {{employee.position}}</p>
            <router-link v-bind:to="{name:'view-employee', params:{employee_id:employee.emp_id}}"
             class="secondary-content">
             <i class="fa fa-eye"></i>
             </router-link>
            </li>
          </ul>  

        <div class="fixed-action-btn">
             <router-link to="/new" class="btn-floating btn-large waves-effect waves-light orang">
                  <i class="fa fa-plus"></i>
             </router-link>
        </div>
        
        <!-- <ul class="collection with-header"> this is the totrial designe
            <li class="collection-header"><h4>Employee</h4></li>
            <li v-for="employee in employees" v-bind:key="employee.id"
            class="collection-item">
            <div class="chip">{{employee.dept}}</div>
            {{employee.emp_id}}:{{employee.name}}
            </li> 
        </ul> -->
 
    </div>
    
</template>

<script>
import db from './firebaseInit';

export default {
    name:'dashboard',
    data(){
        return{
            employees:[]
            
        }
    },

    created(){
        db.collection('employees').orderBy('dept').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
           // console.log(doc.data());
                const data = {
                    'id':doc.id,
                    'emp_id':doc.data().emp_id,
                    'name':doc.data().name,
                    'dept':doc.data().dept,
                    'position':doc.data().position
                }
                this.employees.push(data)
            })
        })
    }
    
}
</script>




