<template>
    <div id="new-employee">
        <h3>New Employee</h3>
        <div class="row">
            <form @submit.prevent="saveEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input id="employeeId" type="text" v-model="employee_id" required>
                        <label for="employeeId">Employee ID</label>
                    </div>
                </div>

                 <div class="row">
                    <div class="input-field col s12">
                        <input id="name" type="text" v-model="name" required>
                        <label for="name">Name</label>
                    </div>
                </div>

                 <div class="row">
                    <div class="input-field col s12">
                        <input id="dept" type="text" v-model="dept" required>
                        <label for="dept">Department</label>
                    </div>
                </div>

                 <div class="row">
                    <div class="input-field col s12">
                        <input id="position" type="text" v-model="position" required>
                        <label for="position">Position</label>
                    </div>
                </div>
                <router-link to="/" class="btn grey waves-effect waves-light">cancel</router-link>
                <button class="btn waves-effect waves-light" type="submit" >Submit</button>
            </form>
        </div>
    </div>
</template>

<script>
import db from'./firebaseInit'

export default {
    name:'new-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            dept:null,
            position:null
        }
    },
    methods:{
        saveEmployee(){
            db.collection('employees').add({
               emp_id: this.employee_id,
               name: this.name,
               dept: this.dept,
               position: this.position 
            }).then(docRef => this.$router.push('/') )
              .catch(eror => console.log(err))
        }
    }
    
}
</script>

<style scoped>

</style>




