<template>

    <transition name="fade">
     <div id="cont">
        <div v-if="show" class="preloader-wrapper active">
            <div class="spinner-layer spinner-orang-only">
                   <div class="circle-clipper left">
                        <div class="circle"></div>
                    </div>
                    <div class="gap-patch">
                        <div class="circle"></div>
                    </div>
                    <div class="circle-clipper right">
                        <div class="circle"></div>
                    </div>
            </div>
        </div>
     </div>
    </transition>
    
</template>

<script>
import { setTimeout } from 'timers';
export default {
    name:'preloader',
    data(){
        return{
            show:true,
        }
    },
    mounted(){
        this.showToggle()
    },
    methods:{
        showToggle(){
           setTimeout(()=>{
               this.show = false
           },1000) 
        }
    }
}
</script>

<style scoped>
#cont{
    padding: 250px 50%;
}
</style>



