<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>

        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <label for="employeeId">Employee ID</label>
                    <div class="input-field col s12">
                        <input disabled id="employeeId" type="text" v-model="employee_id" required>
                    </div>
                </div>

                 <div class="row">
                    <label for="name">Name</label>
                    <div class="input-field col s12">
                        <input id="name" type="text" v-model="name" required>
                    </div>
                </div>

                 <div class="row">
                    <label for="dept">Department</label>
                    <div class="input-field col s12">
                        <input id="dept" type="text" v-model="dept" required>
                    </div>
                </div>

                 <div class="row">
                    <label for="position">Position</label>
                    <div class="input-field col s12">
                        <input id="position" type="text" v-model="position" required>
                    </div>
                </div>
                <router-link to="/" class="btn grey waves-effect waves-light">cancel</router-link>
                <button class="btn waves-effect waves-light" type="submit" >update</button>
            </form>
        </div>
        
        
    </div>
</template>

<script>
import db from'./firebaseInit'
export default {
    name:'edit-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            dept:null,
            position:null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('employees').where('emp_id','==',
        to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                  vm.employee_id = doc.data().emp_id
                  vm.name = doc.data().name
                  vm.dept = doc.data().dept
                  vm.position = doc.data().position
                })
            })
        })
    },
    watch:{
        '$route' : 'fetchData'
    },
    methods:{
        fetchData(){
            db.collection('employees').where('emp_id', '==', 
            this.$route.params.emp_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.employee_id = doc.data().emp_id,
                    this.name = doc.data().name,
                    this.dept = doc.data().dept,
                    this.position = doc.data().position
                })
            })
        },
        updateEmployee(){
            db.collection('employees').where('emp_id','==',
            this.$route.params.employee_id)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        emp_id: this.employee_id,
                        name: this.name,
                        dept: this.dept,
                        position: this.position
                    }).then(()=>{
                        this.$router.push({name:'view-employee',
                         params:{employee_id: this.employee_id}})
                    })
                })
            })
        }

    }
    
}
</script>

<style scoped>

</style>




