<template>
    <div id="view-employee">
        <ul class="collection with-header">
            <li class="collection-header"><h5>Employee Information</h5></li>
            <li class="collection-item">
                 Name : {{name}}
            </li>
            <li class="collection-item">
                 ID : {{employee_id}}
            </li>
            <li class="collection-item">
                 Department : {{dept}}
            </li>
            <li class="collection-item">
                 Position : {{position}}
            </li>
        </ul>
        
        <router-link to="/" class="btn black waves-effect waves-light">Back</router-link>
        <button @click="deleteEmployee" class="btn red waves-effect waves-light">delete</button>
        <router-link v-bind:to="{name:'edit-employee', params:{employee_id:employee_id}}"  class="btn waves-effect waves-light orang">edit</router-link>
    </div>
</template>

<script>
import db from'./firebaseInit';
export default {
    name:'view-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            dept:null,
            position:null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('employees').where('emp_id','==',
        to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              next(vm => {
                  vm.employee_id = doc.data().emp_id
                  vm.name = doc.data().name
                  vm.dept = doc.data().dept
                  vm.position = doc.data().position
              })  
            })
        })
        
    },
    watch:{
        '$route' : 'fetchData'
    },
    methods:{
        fetchData(){
            db.collection('employees').where('emp_id','==',this.$route.params.employee_id
            ).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.employee_id = doc.data().emp_id
                    this.name = doc.data().name
                    this.dept = doc.data().dept
                    this.position = doc.data().position
                })
            })
        },
        deleteEmployee(){
          if(confirm('Are you sure?')){
              db.collection('employees').where('emp_id','==',this.$route.params.employee_id
              ).get().then(querySnapshot => {
                  querySnapshot.forEach(doc => {
                      doc.ref.delete()
                      this.$router.push('/') 
                  })
              })
          }
        }
    }
    
}
</script>

<style scoped>

</style>




